<template>
  <div class="q-pt-none row">
    <q-btn
      class="col-4 q-my-none q-py-none"
      flat
      dense
      v-for="maintain in maintains"
      :key="maintain.unique_code"
      :label="maintain.name + ' ' + maintain.count"
      @click="onMaintainClick(maintain.unique_code, maintain)"
    />
  </div>
</template>

<script>
import detail from "@/components/detail.vue";
export default {
  name: "HelloWorld",
  components: {},
  data() {
    return {
      maintains: [],
    };
  },
  mounted() {
    this.refresh();
  },
  watch: {
    "$store.state.currentWorkshop": function () {
      this.refresh();
    },
  },
  methods: {
    refresh() {
      this.$http
        .get("maintain", {
          params: {
            workshop_unique_code: this.$store.state.currentWorkshop
              ? this.$store.state.currentWorkshop
              : undefined,
          },
        })
        .then((res) => {
          this.maintains = res.data.data;
        });
    },
    onMaintainClick(code, point) {
      if (this.$store.state.currentWorkshop) {
        this.$store.state.currentStation = code;
        this.$store.state.currentPoint = point;
        this.showDetail();
        return;
      }
      this.$store.state.currentWorkshop = code;
      this.$store.state.currentPoint = point;
      console.log(point, this.$store.state.currentPoint);
    },
    showDetail() {
      this.$q
        .dialog({
          component: detail,
          parent: this,
        })
        .onOk(() => {})
        .onCancel(() => {})
        .onDismiss(() => {});
    },
  },
};
</script>