<template>
  <q-dialog ref="dialog" @hide="onDialogHide" full-height full-width>
    <q-card>
      <q-card-section class="row items-center q-pb-none">
        <div class="text-h6"></div>
        <q-space />
        <q-btn icon="close" flat round dense v-close-popup />
      </q-card-section>

      <q-card-section class="q-pa-sm">
        <q-table
          ref="mytable"
          style="height: calc(100vh - 130px)"
          :loading="loading !== 2"
          dense
          title="设备列表"
          :data="data"
          :columns="columns"
          row-key="id"
          binary-state-sort
          :pagination="{ rowsPerPage: 15 }"
        >
          <template v-slot:body="props">
            <q-tr
              :props="props"
              :class="over && props.row.is_overdue ? 'text-red' : ''"
            >
              <q-td key="unique_code" :props="props" auto-width>
                <q-btn
                  flat
                  :label="props.row.unique_code"
                  color="primary"
                  dense
                  @click="showInfo(props.row.unique_code, props.row.url)"
                />
              </q-td>
              <q-td key="category_name" :props="props" auto-width>
                {{ props.row.category_name }}
              </q-td>
              <q-td key="entire_model_name" :props="props" auto-width>
                {{ props.row.entire_model_name }}
              </q-td>
              <q-td key="sub_model_name" :props="props" auto-width>
                {{ props.row.sub_model_name }}
              </q-td>
              <q-td key="workshop_name" :props="props" auto-width>
                {{ props.row.workshop_name }}
              </q-td>
              <q-td key="station_name" :props="props" auto-width>
                {{ props.row.station_name }}
              </q-td>
              <q-td key="installed_at" :props="props" auto-width>
                {{ props.row.installed_at }}
              </q-td>
              <q-td key="due_date_at" :props="props" auto-width>
                {{ props.row.due_date_at }}
              </q-td>
              <q-td key="state" :props="props" auto-width>
                {{ status_dict[props.row.state] }}
              </q-td>
            </q-tr>
          </template>
        </q-table>
      </q-card-section>
    </q-card>
  </q-dialog>
</template>

<script>
export default {
  name: "detail",
  props: {
    ucode: {
      type: String,
      default: "",
    },
    status: {
      type: String,
      default: "",
    },
    cat: {
      type: String,
      default: "",
    },
    over: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    let workshop = this.$store.state.currentWorkshop;
    let station = this.$store.state.currentStation;
    return {
      loading: 0,
      columns: [
        {
          align: "left",
          name: "unique_code",
          label: "设备编码",
          field: "unique_code",
        },
        { name: "category_name", label: "种类", field: "category_name" },
        {
          name: "entire_model_name",
          label: "类型",
          field: "entire_model_name",
        },
        { name: "sub_model_name", label: "型号", field: "sub_model_name" },
        { name: "workshop_name", label: "车间", field: "workshop_name" },
        { name: "station_name", label: "车站", field: "station_name" },
        {
          name: "installed_at",
          label: "安装日期",
          field: "installed_at",
        },
        {
          name: "due_date_at",
          label: "报废日期",
          field: "due_date_at",
          sortable: true,
        },
        { name: "state", label: "状态", field: "state" },
      ],
      status_dict: {
        FIXED: "车间备品",
        FIXING: "入所",
        INSTALLED: "上道",
        INSTALLING: "现场备品",
        SCENE_STANDBY: "现场备品",
        SEND_REPAIR: "送修",
        STANDBY: "车间备品",
        UNINSTALLED: "下道",
        WAIT_IN_WAREHOUSE: "入所",
        SCRAP: "报废",
      },
      data: [],
      params: {
        state_name: this.status,
        workshop_unique_code: workshop,
        station_unique_code: station,
        category_unique_code: this.cat,
        entire_model_unique_code: "",
        sub_model_unique_code: "",
      },
    };
  },
  mounted() {
    this.refresh();
  },
  methods: {
    refresh() {
      this.loading = 0;
      this.data = [];
      let _data1 = this.$http.get(
        "https://hefei-warehouse.zhongchengkeshi.com/api/monitor/material",
        {
          params: this.params,
        }
      );
      // .then((res) => {
      //   this.data.push(...res.data.data);
      //   this.loading += 1;
      //   console.log(this.$refs.mytable);
      //   this.$refs.mytable.sort("due_date_at");
      // });
      let _data2 = this.$http.get(
        "https://hefei.zhongchengkeshi.com/api/monitor/entireInstance",
        {
          params: this.params,
        }
      );
      // .then((res) => {
      //   this.data.push(...res.data.data);
      //   this.loading += 1;
      // });

      Promise.all([_data1, _data2]).then(([res1, res2]) => {
        this.loading = 2;
        this.data.push(...res1.data.data);
        this.data.push(...res2.data.data);
        if (this.over) {
          this.$refs.mytable.sort("due_date_at");
        }
      });
    },
    showInfo(code, url) {
      console.log(url);
      this.$http.get(url).then((res) => {
        this.$store.state.device = code;
        if (res.data.data.entire_instance) {
          let data = res.data.data.entire_instance;
          let tmp = {
            info: {
              unique_code: data.identity_code,
              category_name: data.category_name,
              entire_model_name: data.entire_model.name,
              sub_model_name: data.sub_model.name,
              due_date_at: data.next_fixing_day,
              factory_name: data.factory_name,
              ex_factory_at: data.made_at,
              state: data.status,
              location_name: data.location_name,
              workshop_name: data.maintain_workshop_name,
              station_name: data.maintain_station_name,
            },
            material_logs: data.entire_instance_logs,
            standbies: {},
          };

          if (data.status === "上道") {
            this.$http
              .post(
                "https://hefei.zhongchengkeshi.com/api/pda/sparesStatistics",
                {
                  identity_code: code,
                }
              )
              .then((res) => {
                tmp.standbies = res.data.data;
                this.$store.state.deviceDetail = tmp;
              });
          } else {
            this.$store.state.deviceDetail = tmp;
          }
        } else {
          this.$store.state.deviceDetail = res.data.data;
        }
      });
      this.$refs.dialog.hide();
    },
    show() {
      this.$refs.dialog.show();
    },
    hide() {
      this.$refs.dialog.hide();
    },
    onDialogHide() {
      this.$emit("hide");
    },
    onOKClick() {
      this.$emit("ok");
      this.hide();
    },
    onCancelClick() {
      this.hide();
    },
  },
};
</script>

