import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import './quasar'
import AmapVue from '@amap/amap-vue'

import ECharts from 'vue-echarts'
import { use } from 'echarts/core'
import './api/axios'
import {
  CanvasRenderer
} from 'echarts/renderers'
import {
  BarChart
} from 'echarts/charts'
import {
  GridComponent,
  TooltipComponent
} from 'echarts/components'

use([
  CanvasRenderer,
  BarChart,
  GridComponent,
  TooltipComponent
]);

// 全局注册组件（也可以使用局部注册）
Vue.component('v-chart', ECharts)

AmapVue.config.version = '2.0'
AmapVue.config.key = 'c1735a0fbc6410dbd65afe9112eddeb2'
Vue.use(AmapVue)

Vue.config.productionTip = false

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
