<template>
  <v-chart
    style="height: calc((100vh - 290px) / 3)"
    :option="data"
    @click="showDetail"
    autoresize
  />
</template>

<script>
import detail from "@/components/detail.vue";
import { use } from "echarts/core";
import { CanvasRenderer } from "echarts/renderers";
import { PieChart } from "echarts/charts";
import {
  TitleComponent,
  TooltipComponent,
  LegendComponent,
  DataZoomComponent,
} from "echarts/components";
import VChart, { THEME_KEY } from "vue-echarts";

use([
  CanvasRenderer,
  PieChart,
  TitleComponent,
  TooltipComponent,
  LegendComponent,
  DataZoomComponent,
]);

export default {
  name: "HelloWorld",
  components: {
    VChart,
  },
  provide: {
    [THEME_KEY]: "light",
  },
  data() {
    return {
      data: {
        color: ["#9FE6B8", "#fac858"],
      },
    };
  },
  mounted() {
    this.refresh();
  },
  watch: {
    "$store.state.currentWorkshop": function () {
      this.refresh();
    },
    "$store.state.currentStation": function () {
      this.refresh();
    },
  },
  methods: {
    refresh() {
      this.$http
        .get("scraped", {
          params: {
            workshop_unique_code: this.$store.state.currentWorkshop
              ? this.$store.state.currentWorkshop
              : undefined,
            station_unique_code: this.$store.state.currentStation
              ? this.$store.state.currentStation
              : undefined,
          },
        })
        .then((res) => {
          let { x, statistics, overdue_statistics } = res.data.data;
          let tmp_data = {
            textStyle: {
              color: "#FFFFFF",
            },
            color: ["#9FE6B8"],
            tooltip: {
              trigger: "axis",
              axisPointer: {
                type: "shadow",
                label: {
                  show: true,
                },
              },
            },
            calculable: true,
            legend: {
              data: ["总数", "超期使用"],
              itemGap: 5,
              textStyle: {
                color: "#FFFFFF",
              },
            },
            grid: {
              left: "3%",
              right: "3%",
              bottom: "3%",
              containLabel: true,
            },
            xAxis: [
              {
                type: "category",
                data: x,
              },
            ],
            yAxis: [
              {
                type: "value",
                name: "",
                axisLabel: {},
              },
            ],
            series: [
              {
                name: "总数",
                type: "bar",
                label: {
                  show: true,
                  position: "top",
                },
                data: statistics,
              },
              {
                name: "超期使用",
                type: "bar",
                data: overdue_statistics,
                label: {
                  show: true,
                  position: "top",
                },
              },
            ],
          };
          this.data = tmp_data;
        });
    },
    showDetail(e) {
      let cat = this.$store.state.cats.find((a) => a.name == e.name);
      this.$q
        .dialog({
          component: detail,
          parent: this,
          cat: cat.unique_code,
          over: true,
        })
        .onOk(() => {})
        .onCancel(() => {})
        .onDismiss(() => {});
    },
  },
};
</script>