<script>
import coordtransform from "coordtransform";
import _markerIcon from "@/assets/pin.png";
import lines from "@/static/lines";
import lefttop from "@/components/lefttop.vue";
import leftmiddle from "@/components/leftmiddle.vue";
import leftbottom from "@/components/leftbottom.vue";
import righttop from "@/components/righttop.vue";
import rightmiddle from "@/components/rightmiddle.vue";
import rightbottom from "@/components/rightbottom.vue";
export default {
  components: {
    lefttop,
    leftmiddle,
    leftbottom,
    righttop,
    rightmiddle,
    rightbottom,
  },
  data() {
    return {
      markerIcon: { image: _markerIcon, imageSize: [26, 28] },
      stationPoints: [],
      workshopPoints: [],
      standbypoints: [],
      lines: lines.data,
      time: "",
      color: ["blue", "red", "green", "purple"],
      center: [117.04546, 31.829606],
      // position: [117.04546, 31.829606],
      zoom: 9,
      loc: [],
      instance: {},
      logs: [],
      columns: [
        {
          name: "name",
          align: "left",
          label: "车间/车站",
          field: "name",
        },
        {
          name: "count",
          align: "left",
          label: "备品数量",
          field: "count",
        },
        {
          name: "distance",
          align: "left",
          label: "距离",
          field: "distance",
          format: (val) => `${val}KM`,
        },
      ],
      render: [
        ["唯一编码", "unique_code"],
        ["种类", "category_name"],
        ["类型", "entire_model_name"],
        ["型号", "sub_model_name"],
        ["使用年限", "service_life"],
        ["到期时间", "due_date_at"],
        ["供应商", "factory_name"],
        ["版本号", "version_number"],
        ["出厂日期", "ex_factory_at"],
        ["仓库位置", "location_name"],
        ["安装位置", "install_location_name"],
        ["状态", "state"],
        ["车间", "workshop_name"],
        ["车站", "station_name"],
      ],
      mode: "polyline",
    };
  },
  watch: {
    "$store.state.currentStation": function () {
      this.$store.state.device = "";
    },
    "$store.state.currentWorkshop": function () {
      this.$store.state.device = "";
    },
    "$store.state.deviceDetail": function (e) {
      this.instance = e.info;
      let tmp = Object.values(e.material_logs);
      this.logs = tmp.flat();
      this.loc = Object.values(e.standbies).flat();
      this.standbypoints = Object.values(e.standbies).flat();
      console.log(this.standbypoints);
    },
  },
  mounted() {
    this.$http.get("basicInfo").then((res) => {
      let tmp = {};
      this.stationPoints = [...res.data.data.stationPoints];
      this.workshopPoints = [...res.data.data.workshopPoints];
      res.data.data.statuses.map((a) => {
        tmp[a.unique_code] = a.name;
      });
      console.log(tmp);
      this.$store.state.cats = res.data.data.kinds.map((a) => ({
        ...a,
        subs: null,
      }));
    });
    setInterval(() => {
      this.time = new Date();
    }, 1000);
  },
  methods: {
    calc(lon, lat) {
      return coordtransform.bd09togcj02(lon, lat);
    },
    async test() {
      // await this.$http.get("rightBottom");
      this.$store.state.currentStation = "G41231";
      // this.zoom = 11;
      // this.$store.state.device = "aaa";
    },
    onStationClick(e, point) {
      this.$store.state.currentStation = e;
      this.$store.state.currentWorkshop = point.workshop_unique_code;
      this.$store.state.currentPoint = point;
    },
    onWorkshopClick(e, point) {
      this.$store.state.currentWorkshop = e;
      this.$store.state.currentStation = "";
      this.$store.state.currentPoint = point;
    },
    reset() {
      this.zoom = 9;
      this.$store.state.device = "";
    },
    // onHotspotClick(e) {
    //   if (e && e.lnglat) {
    //     this.center = [e.lnglat.lng, e.lnglat.lat];
    //   }
    // },
  },
  beforeDestroy() {
    clearInterval();
  },
};
</script>

