<template>
  <v-chart
    style="height: calc((100vh - 290px) / 3)"
    :option="data"
    @click="showDetail"
    autoresize
  />
</template>

<script>
import detail from "@/components/detail.vue";
import { use } from "echarts/core";
import { CanvasRenderer } from "echarts/renderers";
import { PieChart } from "echarts/charts";
import {
  TitleComponent,
  TooltipComponent,
  LegendComponent,
  DataZoomComponent,
} from "echarts/components";
import VChart, { THEME_KEY } from "vue-echarts";

use([
  CanvasRenderer,
  PieChart,
  TitleComponent,
  TooltipComponent,
  LegendComponent,
  DataZoomComponent,
]);

export default {
  name: "HelloWorld",
  components: {
    VChart,
  },
  provide: {
    [THEME_KEY]: "light",
  },
  data() {
    return {
      status: {
        FIXED: "车间备品",
        FIXING: "入所",
        INSTALLED: "上道",
        INSTALLING: "现场备品",
        SCENE_STANDBY: "现场备品",
        SEND_REPAIR: "送修",
        STANDBY: "车间备品",
        UNINSTALLED: "下道",
        WAIT_IN_WAREHOUSE: "入所",
      },
      data: {},
    };
  },
  mounted() {
    this.refresh();
  },
  watch: {
    "$store.state.currentWorkshop": function () {
      this.refresh();
    },
    "$store.state.currentStation": function () {
      this.refresh();
    },
  },
  methods: {
    refresh() {
      this.$http
        .get("status", {
          params: {
            workshop_unique_code: this.$store.state.currentWorkshop
              ? this.$store.state.currentWorkshop
              : undefined,
            station_unique_code: this.$store.state.currentStation
              ? this.$store.state.currentStation
              : undefined,
          },
        })
        .then((res) => {
          let tmp = [];
          const { statistics, x } = res.data.data;
          statistics.map((a, index) => {
            tmp[index] = {
              value: a,
              name: `${this.status[x[index]]}:${a}`,
              param: x[index],
            };
          });
          let tmp_data = {
            color: [
              "#9FE6B8",
              "#FFDB5C",
              "#FF9F7F",
              "#FB7293",
              "#8378EA",
              "#37A2DA",
            ],
            legend: {
              orient: "vertical",
              x: "left",
              top: "20%",
              left: "5%",
              textStyle: {
                fontSize: "14",
                color: "#FFFFFF",
              },
            },
            series: [
              {
                type: "pie",
                radius: ["50%", "70%"],
                center: ["60%", "50%"],
                avoidLabelOverlap: false,
                label: {
                  position: "center",
                  show: false,
                },
                emphasis: {
                  label: {
                    show: true,
                    fontSize: "18",
                    fontWeight: "bold",
                  },
                },
                labelLine: {
                  show: true,
                },
              },
            ],
          };
          tmp_data.series[0].data = tmp;
          this.data = tmp_data;
        });
    },
    showDetail(e) {
      console.log(e);
      this.$q
        .dialog({
          component: detail,
          parent: this,
          status: e.name.split(":")[0],
        })
        .onOk(() => {})
        .onCancel(() => {})
        .onDismiss(() => {});
    },
  },
};
</script>